import { _decorator, Component, Node, tween, Vec3 } from "cc";
import { sleep } from "../Promises";
const { ccclass, property } = _decorator;

@ccclass("EffectManager")
export class EffectManager extends Component {
  public static async decreaseScale(
    target: Node,
    duration: number,
    yoyo: boolean
  ): Promise<void> {
    return new Promise<void>((resolve) => {
      tween(target)
        .to(duration, { scale: Vec3.ZERO })
        .call(() => {
          resolve();
          if (yoyo) {
            tween(target).to(duration, { scale: Vec3.ONE }).start();
          }
        })
        .start();
    });
  }

  public static async movePiece(
    target: Node,
    to: Vec3,
    duration: number,
  ): Promise<void> {
    return new Promise<void>((resolve) => {
      tween(target)
        .to(duration, {
           position: to 
           //new Vec3(
          //   target.position.x + direction.x * 100,
          //   target.position.y + direction.y * 100, //buradaki 100'ü sprite brimi boyutuna göre almalıyız dinamik yap!!!
          //   target.position.z + direction.z * 100
          // ),
        })
        .call(() => {
          resolve();
        })
        .start();
    });
  }

  public static async fallPieceToGridPosition(
    target: Node,
    to: Vec3,
    duration: number,
    delay: number
  ): Promise<void> {
    await sleep(delay);
    return new Promise<void>((resolve) => {
      tween(target)
        .to(duration, { position: to })
        .call(() => {
          resolve();
        })
        .start();
    });
  }
}
